<template>
  <div class="route-wrapper">
    <div class="route-head">
      <Breadcrumb separator-class="el-icon-arrow-right">
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
    </div> 
    <div class="route-content">
      <Button type="primary" size="small" icon="el-icon-delete"></Button>
      <Select placeholder="" v-model="addrSelected" size="small" class="my-select">
        <Option 
          v-for="(addr,index) in addrOptions" 
          :key="index"
          :label="addr.label"
          :value="addr.value"
        ></Option>
      </Select>
      <MyInput 
        class="my-input"
        v-model="userInput" 
        placeholder=""
        size="small"
      ></MyInput>
      <Button type="primary" size="small" icon="el-icon-search"></Button>
      <MyTable
        size="medium"
        style="margin-top:2rem;text-align:center;"
        :data="persons.list"
        :border="true"
      >
        <TableColumn type="selection" align="center" min-width="20"></TableColumn>
        <TableColumn prop="id" align="center" label="ID" min-width="35"></TableColumn>
        <TableColumn prop="name" align="center" label="" min-width="50"></TableColumn>
        <TableColumn prop="money" align="center" label="" min-width="50"></TableColumn>
        <TableColumn prop="address" align="center" label="" min-width="150"></TableColumn>
        <TableColumn prop="state" align="center" label="" min-width="50">
          <template slot-scope="scope">
            <Tag :type="scope.row.state == '' ? 'success':'danger'">{{scope.row.state}}</Tag>
          </template>
        </TableColumn>
        <TableColumn prop="date" align="center" label="" min-width="150"></TableColumn>
        <TableColumn label="" align="center" min-width="100">
          <Button type="primary" size="mini"></Button>
          <Button type="danger" size="mini"></Button>
        </TableColumn>
      </MyTable>
      <Pagination
        background
        style="margin:2rem;"
        layout="prev, pager, next"
        :page-count="persons.pageTotal">
      </Pagination>
    </div>
  </div>

</template>
