  import {mapActions,mapState} from 'vuex'
  import {Button,Select,Option,Input,Breadcrumb,
          BreadcrumbItem,Table,TableColumn,Tag,Pagination} from 'element-ui'
  export default {
      name:'Table',
      components:{Button,
                  Select,
                  Option,
                  MyInput:Input,
                  Breadcrumb,
                  BreadcrumbItem,
                  MyTable:Table,
                  TableColumn,
                  Pagination,
                  Tag},
      data(){
        return {
          userInput:'',
          addrSelected:'',
          tableData: [
            {
              id:'1',
              userName:'',
              balance:123,
              address:'',
              status:'',
              registered:'2011-2-3',
            },
            {
              id:'2',
              userName:'',
              balance:23,
              address:'',
              status:'',
              registered:'2011-2-3',
            },
            {
              id:'3',
              userName:'',
              balance:12334,
              address:'',
              status:'',
              registered:'2011-2-3',
            }
          ],
            multipleSelection: [],
            addrOptions:[
              {
                label:'',
                value:'guangdong'
              },
              {
                label:'',
                value:'yunnan'
              },
            ]
        }
      },
      computed:{
        ...mapState('personAbout',['persons'])
      },
      methods:{
        ...mapActions('personAbout',['getPersonList']),
        handleSelectionChange(){
        }
      },
      mounted(){
        !this.persons.list.length && this.getPersonList();
      }
  }
</script>
<style scoped>
  .el-pagination{
    text-align:center;
  }
  .cell {
    text-align:center;
  }
  .my-select {
    width:10rem;
    margin:0 3rem;
  }
  .my-input{
    width:30rem;
    margin:0 3rem;
    margin-left:0;
  }
</style>
